import React from "react";
import { ImageWithRelativeSize } from "../components";
import { Colors } from "../config";

interface Props {
  src: string;
  onClick?: React.MouseEventHandler<HTMLImageElement>;
  borderColor?: Colors;
}

const ClickableImage: React.SFC<Props> = ({ src, onClick, borderColor }) => (
  <ImageWithRelativeSize
    url={src}
    onClick={onClick}
    border={borderColor || true}
  />
);

export default ClickableImage;



