import React from "react";
import {
  ArtistPreview,
  ArtistGaleryHeader,
  VideosSection
} from "../components";
import { LayoutProps } from "../hoc/withLayout";
import { chris } from "./Artists";
import { Colors } from "../config";
import showcaseImage from "../images/artists/gallery/GalerieGross-3.jpg";

type Props = LayoutProps;

const Chris: React.SFC<Props> = () => (
  <React.Fragment>
    <ArtistGaleryHeader
      title={chris.name}
      leftLinkColor={Colors.neotraditional}
      leftLinkTo="/tilo"
      rightLinkColor={Colors.aquarelle}
      rightLinkTo="/biggi"
      showcaseImage={showcaseImage}
    />
    <ArtistPreview
      imageData={chris.image}
      heading={chris.name}
      subHeading={chris.company}
      color={chris.color}
    >
      {chris.text}
    </ArtistPreview>
    <VideosSection />
  </React.Fragment>
);

export default Chris;



