import React from "react";
import styled from "react-emotion";
import { Colors } from "../config";
import { withWindowSize, WindowSizeProps } from "../hoc";

interface ImageProps {
  wrapperWidth: number;
  border?: boolean | Colors;
  isClickable?: boolean;
}

const Image = styled<ImageProps, "img">("img")`
  max-width: ${p => p.wrapperWidth}px;
  box-sizing: border-box;
  ${p =>
    p.border &&
    `border: 5px solid ${
      typeof p.border !== "string" ? p.theme.colors.primary : p.border
    };`};
  ${p => p.isClickable && `cursor: pointer`};
`;

export interface Props extends WindowSizeProps {
  url: string;
  border?: boolean | Colors;
  onClick?: React.MouseEventHandler<HTMLImageElement>;
}

interface State {
  readonly width: number;
}

class ImageWithRelativeSize extends React.Component<Props, State> {
  ref: React.RefObject<HTMLDivElement>;

  constructor(props: Props) {
    super(props);

    this.ref = React.createRef<HTMLDivElement>();
    this.state = {
      width: 0
    };
  }

  componentDidMount() {
    this.setWidth();
  }

  componentDidUpdate(prevProps: Props) {
    if (this.props.width !== prevProps.width) {
      this.setWidth();
    }
  }

  setWidth = () => {
    if (this.ref.current) {
      this.setState({
        width: this.ref.current.clientWidth
      });
    }
  };

  render(): React.ReactNode {
    return (
      <div ref={this.ref}>
        <Image
          src={this.props.url}
          border={this.props.border}
          wrapperWidth={this.state.width}
          onClick={this.props.onClick}
          isClickable={this.props.onClick !== undefined}
        />
      </div>
    );
  }
}

export default withWindowSize(ImageWithRelativeSize);



