import React from "react";
import { Row, Col } from "react-flexbox-grid";
import styled from "react-emotion";
import { Link } from "react-router-dom";
import { PageHeading } from "../components";
import { Colors } from "../config";

const BigImage = styled("img")`
  width: 100%;
  border: 5px solid ${p => p.theme.colors.primary};
`;

interface ArrowLinkProps {
  color: Colors;
}
const ArrowLink = styled<ArrowLinkProps, "div">("div")`
  font-size: 4rem;
  cursor: pointer;
  a {
    text-decoration: none;
    &:hover,
    &:active {
      color: ${p => p.theme.colors.text};
    }
    &:link,
    &:visited {
      color: ${p => p.color};
    }
  }
`;

interface Props {
  title: string;
  leftLinkTo: string;
  rightLinkTo: string;
  leftLinkColor: Colors;
  rightLinkColor: Colors;
  showcaseImage?: string;
}

const ArtistGaleryHeader: React.SFC<Props> = ({
  title,
  leftLinkColor,
  leftLinkTo,
  rightLinkColor,
  rightLinkTo,
  showcaseImage
}) => (
  <React.Fragment>
    <Row
      center="xs"
      middle="xs"
      style={{ marginBottom: showcaseImage ? "1rem" : "5rem" }}
    >
      <Col xs={1}>
        <ArrowLink color={leftLinkColor}>
          <Link to={leftLinkTo}>{"◄"}</Link>
        </ArrowLink>
      </Col>
      <Col xs={10}>
        <PageHeading>{title}</PageHeading>
      </Col>
      <Col xs={1}>
        <ArrowLink color={rightLinkColor}>
          <Link to={rightLinkTo}>{"►"}</Link>
        </ArrowLink>
      </Col>
    </Row>
    {showcaseImage && (
      <Row center="xs" style={{ marginBottom: "5rem" }}>
        <Col xs>
          <BigImage src={showcaseImage} />
        </Col>
      </Row>
    )}
  </React.Fragment>
);

export default ArtistGaleryHeader;



