import React from "react";
import {
  ArtistPreview,
  VideosSection,
  ArtistGaleryHeader
} from "../components";
import { LayoutProps } from "../hoc/withLayout";
import { biggi } from "./Artists";
import { Colors } from "../config";
import showcaseImage from "../images/artists/gallery/GalerieGross-2.jpg";

type Props = LayoutProps;

const Biggi: React.SFC<Props> = () => (
  <React.Fragment>
    <ArtistGaleryHeader
      title={biggi.name}
      leftLinkColor={Colors.fineline}
      leftLinkTo="/chris"
      rightLinkColor={Colors.neotraditional}
      rightLinkTo="/tilo"
      showcaseImage={showcaseImage}
    />
    <ArtistPreview
      imageData={biggi.image}
      heading={biggi.name}
      subHeading={biggi.company}
      color={biggi.color}
    >
      {biggi.text}
    </ArtistPreview>
    <VideosSection />
  </React.Fragment>
);

export default Biggi;



