import React from "react";
import {
  ArtistPreview,
  VideosSection,
  ArtistGaleryHeader
} from "../components";
import { LayoutProps } from "../hoc/withLayout";
import { tilo } from "./Artists";
import { Colors } from "../config";
import showcaseImage from "../images/artists/gallery/GalerieGross.jpg";

type Props = LayoutProps;

const Tilo: React.SFC<Props> = () => (
  <React.Fragment>
    <ArtistGaleryHeader
      title={tilo.name}
      leftLinkColor={Colors.aquarelle}
      leftLinkTo="/biggi"
      rightLinkColor={Colors.fineline}
      rightLinkTo="/chris"
      showcaseImage={showcaseImage}
    />
    <ArtistPreview
      imageData={tilo.image}
      heading={tilo.name}
      subHeading={tilo.company}
      color={tilo.color}
    >
      {tilo.text}
    </ArtistPreview>
    <VideosSection />
  </React.Fragment>
);

export default Tilo;



