import React from "react";
import styled from "react-emotion";
import { Link } from "react-router-dom";
import { Row, Col } from "react-flexbox-grid";
import { ImageData, ImageWithRelativeSize } from "../components";
import { Colors } from "../config";

interface TextWrapperProps {
  isReverseText?: boolean;
}

const TextWrapper = styled<TextWrapperProps, "div">("div")`
  display: flex;
  padding: 1rem;
  flex-direction: column;
  ${p =>
    p.isReverseText &&
    `
    align-items: flex-end;
    p {
      text-align: right;
    }
  `};
`;

interface HeadingProps {
  color?: string;
}

const Heading = styled<HeadingProps, "h2">("h2")`
  color: ${p => p.color || p.theme.colors.text};
  font-size: 2.6rem;
  margin: 1rem 0 0.5rem 0;
`;

const SubHeading = styled("h3")`
  margin: 0.5rem 0;
  font-size: 2rem;
  font-weight: 100;
`;

interface GaleryButtonProps {
  color?: Colors;
}
const GaleryLink = styled<GaleryButtonProps, "span">("span")`
  margin: 2rem 0;
  ${p => p.theme.textShadow};
  a {
    text-decoration: none;
    &:link, &: visited {
      color: ${p => (p.color ? p.color : p.theme.colors.primary)};
    }
    &:hover, &:active {
      color: ${p => p.theme.colors.text}
      text-decoration: underline;
    }
  }
`;

interface Props {
  imageData: ImageData;
  heading: string;
  subHeading: string;
  imageAlign?: "left" | "right";
  galeryRoute?: string;
  color?: Colors;
}

const ArtistPreview: React.SFC<Props> = ({
  imageData: { url, ...imageData },
  heading,
  subHeading,
  color,
  imageAlign,
  children,
  galeryRoute
}) => (
  <Row
    reverse={imageAlign && imageAlign === "right"}
    style={{ marginTop: "5rem", marginBottom: "5rem" }}
    id={heading.toLowerCase()}
  >
    <Col sm={6} xs={12}>
      <ImageWithRelativeSize url={url} border={imageData.border} />
    </Col>
    <Col sm={6} xs={12}>
      <TextWrapper isReverseText={imageAlign && imageAlign === "right"}>
        <Heading color={color}>{heading}</Heading>
        <SubHeading>{subHeading}</SubHeading>
        {galeryRoute && (
          <GaleryLink color={color}>
            <Link to={galeryRoute}>Weiter zur Galerie</Link>
          </GaleryLink>
        )}
        {children}
      </TextWrapper>
    </Col>
  </Row>
);

export default ArtistPreview;



