import React from "react";
import { Row, Col } from "react-flexbox-grid";
import { ImageWithRelativeSize } from "../components";

interface Props {
  images: string[][];
}

const Submissions: React.SFC<Props> = ({ images }) => (
  <React.Fragment>
    {images.map((i, index) => (
      <Row around="xs" key={index} style={{ marginBottom: "3rem" }}>
        {i.map((j, innerIndex) => (
          <Col md={3} sm={5} xs={10} key={innerIndex}>
            <ImageWithRelativeSize border url={j} />
          </Col>
        ))}
      </Row>
    ))}
  </React.Fragment>
);

export default Submissions;



