import React from "react";
import { Subtract } from "utility-types";
import { WindowSizeConsumer, WindowSizeProps } from "../components";

const withWindowSize = <WrappedProps extends WindowSizeProps>(
  WrappedComponent: React.ComponentType<WrappedProps>
) => {
  type HocProps = Subtract<WrappedProps, WindowSizeProps>;

  class WithWindowSize extends React.PureComponent<HocProps> {
    render(): React.ReactNode {
      return (
        <WindowSizeConsumer>
          {state => <WrappedComponent {...state} {...this.props} />}
        </WindowSizeConsumer>
      );
    }
  }
  return WithWindowSize;
};

export { WindowSizeProps };
export default withWindowSize;



