import { ThemeProvider } from "emotion-theming";
import History from "history";
import React from "react";
import { aquarelleTheme, finelineTheme, neotraditionalTheme } from "../config";

interface Props {
  location: History.Location;
}

class RouterTheme extends React.Component<Props> {
  componentDidUpdate(prevProps: Props) {
    if (this.props.location !== prevProps.location) {
      window.scrollTo(0, 0);
    }
  }
  render() {
    const { location, children } = this.props;
    let customTheme = neotraditionalTheme;
    if (/^\/(?:fineline|chris)/.test(location.pathname)) {
      customTheme = finelineTheme;
    }
    if (/^\/(?:aquarelle|biggi)/.test(location.pathname)) {
      customTheme = aquarelleTheme;
    }
    return <ThemeProvider theme={customTheme}>{children}</ThemeProvider>;
  }
}

export default RouterTheme;



